* {
    margin: 0;
    padding: 0;
    border: none;

    --color0: #FFFFFF;
    --color1: #D9D9D9;
    --color2: #8A898A;
    --color3: #C71F25;
    --color4: #4785EB;
    --color5: #27AE60;
}

body {
    background-color: var(--color1);
}



/*CONTENT*/
#content {
    display: fixed;
    margin: 10px 5px 2px 17%;
    position: absolute;
    background: var(--color0);
    padding: 20px;
    border-radius: 6px;
    min-width: 790px;
    width: 70%;
}

div#content_header {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
}


/***CARDS***/
table#cards {
    margin-top: 20px;
    width: 100%;
}

#cards tbody,
#cards thead {
    height: 20px;
}

#card-titles th {
    border-bottom: solid 1px #d9d9d9;
}

#cards tr:nth-child(even) {
    background-color: rgba(217, 217, 217, 0.578);
}

tr#card-titles {
    text-align: left;
    font-size: 1.2rem;
    font-family: roboto;
}

.tdSal {
    text-align: right;
}